@import "_vars";
@import "_mixins";

.service {
  margin-top: -269px;
  padding-top: 300px;
  padding-bottom: 83px;
  position: relative;
  z-index: 7;
  background-color: @midnight-blue;
  background-image: url("../img/service/service-bg-mobile.png"), url("../img/service/service-back-mobile.png");

  @media (min-resolution: 2dppx) {
    background-image: url("../img/service/service-bg-mobile@2x.png"), url("../img/service/service-back-mobile@2x.png");
    background-size: 292px auto, 113px auto;
  }

  background-position: calc(100% - 10px) bottom, left calc(100% - 222px);
  background-repeat: no-repeat;
  .borderRadius;

  @media (min-width: @tablet-width) {
    margin-top: -140px;
    padding-top: 273px;
    padding-bottom: 89px;
    background-image: url("../img/service/service-bg-tablet.png"), url("../img/service/service-back-tablet.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/service/service-bg-tablet@2x.png"), url("../img/service/service-back-tablet@2x.png");
      background-size: 376px auto, 100% auto;
    }

    background-position: bottom 89px left 100%, center top 235px;
    background-size: auto, calc(100% - 60px);
  }

  @media (min-width: @desktop-width) {
    padding: 0;
    margin-top: -60px;
    background-image: none;
  }
}

.service__text {
  margin-right: 25px;
  margin-bottom: 20px;
  margin-left: @margin-left;
  color: @white;

  @media (min-width: @tablet-width) {
    max-width: 46%;
    margin-bottom: 31px;
    margin-left: @margin-left-m;
    .fontChanged(28px, 33px);
  }

  @media (min-width: @desktop-width) {
    max-width: 48%;
    margin-bottom: 41px;
    margin-left: @margin-left-xl;
  }

  &--bold {
    display: block;
    margin-bottom: 0;
    font-weight: 700;

    @media (min-width: @tablet-width) {
      margin-bottom: 53px;
      .fontChanged(21px, 25px);
    }

    @media (min-width: @desktop-width) {
      max-width: 425px;
      margin-bottom: 85px;
    }
  }
}

.service__button {
  display: none;
  position: relative;

  &::after {
    .abs(block, 44%, 0, 50%, calc(100% - 45px));
    content: "";
    .size(11px, 13px);
    background-image: url("data:image/svg+xml,%3Csvg width='12' height='14' viewBox='0 0 12 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M12 7L0 13.0622L0 0.937822L12 7Z' fill='%231D2E5B'/%3E%3C/svg%3E%0A");
    .bg(right center, cover);
    transition: @transitionOpacity;
  }

  @media (min-width: @tablet-width) {
    display: block;
    max-width: 275px;
    margin-left: 34px;
  }

  @media (min-width: @desktop-width) {
    margin-left: @margin-left-xl;
  }
}

.service__title {
  margin-left: @margin-left;

  @media (min-width: @tablet-width) {
    margin-bottom: 43px;
    margin-left: 33px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 60px;
    margin-left: @margin-left-xl;
  }
}
