@import "_vars";
@import "_mixins";

.transport-icon {
  .size(20px, 20px);
  .bg(center, contain);

  &--disabled svg {
    opacity: 0.15;
  }

  &--big {
    .size(32px, 32px);
  }

  &--button {
    .size(43px, 43px);
  }

  &--filter {
    .size(32px, 32px);

    @media (min-width: @tablet-width) {
      .size(43px, 43px);
    }
  }
}
