  width: 23px;
  height: 22px;
}
.list-transports__button:hover {
  box-shadow: 0 2px 6px rgba(22, 28, 53, 0.17);
}
.list-transports__button:hover .list-transports__checkbox:checked ~ .list-transports__icon {
  opacity: 0.7;
}
.list-transports__button:hover .list-transports__checkbox:not(:checked) ~ .list-transports__icon {
  fill: #B2B2B2;
}
.list-transports__checkbox:not(:checked) ~ .list-transports__icon {
  opacity: 0.15;
}
.list-transports__checkbox:disabled:checked + .list-transports__button,
.list-transports__checkbox:disabled:not(:checked) + .list-transports__button {
  background-color: #E2E5F2;
}
.list-transports__checkbox:disabled:checked + .list-transports__icon {
  opacity: 0.3;
}
.list-transports__checkbox:disabled:not(:checked) + .list-transports__icon {
  opacity: 0.1;
}
.list-transports__checkbox:focus ~ .list-transports__icon,
.list-transports__checkbox:focus-visible ~ .list-transports__icon {
  outline: 1px solid #161C35;
  border-radius: 50%;
  outline-offset: 12px;
}
