.tooltip {
  position: relative;
}
.tooltip:hover .tooltip__text {
  visibility: visible;
}
.tooltip__text {
  visibility: hidden;
  box-sizing: border-box;
  width: 170px;
  padding: 18px 22px;
  text-align: center;
  font-family: "BloggerSans", "Tahoma", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 20px;
  font-style: normal;
  color: #FFFFFF;
  background-color: #101D41;
  border-radius: 4px;
  position: absolute;
  display: block;
  top: calc(100% + 10px);
  right: initial;
  bottom: initial;
  left: calc(50% - 85px);
  z-index: 5;
  opacity: 1;
}
.tooltip__text::after {
  content: "";
  position: absolute;
  display: block;
  top: initial;
  right: initial;
  bottom: 100%;
  left: 50%;
  margin-left: -6px;
  border-width: 6px;
  border-style: solid;
  border-color: transparent transparent #101D41 transparent;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
